import React, {Component} from 'react';
import Avatar from './images/avatar.jpg';
import Support from './images/support.svg';
import Psd_to_Html from './images/psd_to_html.svg';
import Responsive_Design from './images/responsive_design.svg';
import Web_Dev from './images/web_development.svg';
import Wordpress from './images/wordpress.svg';
import Fylo from './images/fylo.png';
import Dentist from './images/dentist_demo.png';
import Huddle from './images/huddle.png';
import Insure from './images/insure.jpg';

import Clipboard from './images/clipboard.png';
import Price from './images/single_price.png';
import Project from './images/project_tracking.png';
import Cards from './images/cards.png';
import Base from './images/base.png';
import Covid19 from './images/covid19.jpg';


export default class mainContent extends Component {
    render() {
        return (
            <div>
                <main>
                    <div className="container">
                        <div className="box flex">
                            <div className="about-me">
                            <h2>About me</h2>
                            <p>
                                I like to consider myself an optimistic, focused and responsible person in my job. I love helping people discover efficient solutions to their problems.
                            </p> 
                            <p>
                                I'm efficient and highly organized and this enables me to be as productive as possible on the job. I enjoy solving problems, troubleshooting issues, and coming up with solutions in a timely manner.  
                            </p>
                            <p>
                                I like dabbling in various parts of frontend development and like to learn about new technologies, write technical articles or simply play games in my free time.
                            </p>
                            </div>
                    
                            <div className="avatar">
                                <img src={Avatar} alt="avatar"/>
                            </div>
                        </div>
                    </div>    
                    
                    <section className="skills">
                        <div className="row">
                            <div>
                                <div className="flex space-center">
                                    <div className="card">
                                        <div className="info">
                                            <img src={Web_Dev} style={{height:'123px'}} alt="web_dev" />
                                            <h5>Web development</h5>
                                            <p className="text-muted">HTML, CSS (Bootstrap), JavaScript (React, Node, MySQL)? You name it. I have it all covered!</p>
                                        </div>
                                    </div>
                                    <div className="card">
                                        <div className="info">
                                            <img src={Responsive_Design} alt="responsive_design" />
                                            <h5>Responsive Design</h5>
                                            <p className="text-muted">In 2020 you can't go online without having a mobile responsive website. Let me know!</p>
                                        </div>
                                    </div>
                                    <div className="card">
                                        <div className="info">
                                            <img src={Wordpress} alt="wordpress" />
                                            <h5>Wordpress</h5>
                                            <p className="text-muted">Millions of website are built on top of Wordpress, yours can become the next one!</p>
                                        </div>
                                    </div>
                                </div>
                            </div>    
                            <div>
                                <div className="flex space-center">
                                    <div className="card">
                                        <div className="info">
                                            <img src={Psd_to_Html} alt="psd_to_html" />
                                            <h5>PSD to HTML</h5>
                                            <p className="text-muted">Do you have a nice design in Phtoshop, Figma or even sketch? Let me know and I'll convert them into fully responsive, nice looking websites!</p>
                                        </div>
                                    </div>
                                    <div className="card">
                                        <div className="info">
                                            <img src={Support} style={{height:'140px'}} alt="support" />
                                            <h5>Technical support</h5>
                                            <p className="text-muted">Provide enterprise-level assistance to my clients. I will diagnose and troubleshoot problems for helping my clients to install applications.</p>
                                        </div>
                                    </div>
                                </div>
                            </div>    
                        </div>    
                    </section>

                    <section className="project-portfolio" id="project-portfolio">
                    <div className="top-card-feature">
                        <h2>Projects</h2>
                    </div>
                    <div className="container-cards">
                        <div className="box-cards box-one box-push">
                            <a href="https://lungancatalin.github.io/clipboard/?" target="_blank" rel="noopener noreferrer"><img src={Clipboard} alt="clipboard" /></a>
                        </div>
                        <div className="box-cards box-two">
                            <a href="https://lungancatalin.github.io/fylo/" target="_blank" rel="noopener noreferrer"><img src={Fylo} alt="fylo" /></a>
                        </div>  
                        <div className="box-cards box-three box-push">
                            <a href="https://lungancatalin.github.io/dental_studio/" target="_blank" rel="noopener noreferrer"><img src={Dentist} alt="dentist_demo" /></a>
                        </div> 
                        <div className="box-cards box-four">
                            <a href="https://lungancatalin.github.io/insure/" target="_blank" rel="noopener noreferrer"><img src={Insure} alt="insure" /></a>
                        </div>
                    </div>
                </section>

                <section className="project-portfolio">
                    <div className="container-cards">
                        <div className="box-cards box-five box-push">
                            <a href="https://lungancatalin.github.io/project_tracking/" target="_blank" rel="noopener noreferrer"><img src={Project} alt="project_tracking" /></a>
                        </div>
                        <div className="box-cards box-six">
                            <a href="https://lungancatalin.github.io/huddle//" target="_blank" rel="noopener noreferrer"><img src={Huddle} alt="huddle" /></a>
                        </div>  
                        <div className="box-cards box-seven box-push">
                            <a href="https://lungancatalin.github.io/single_price/" target="_blank" rel="noopener noreferrer"><img src={Price} alt="single_price" /></a>
                        </div> 
                        <div className="box-cards box-eight">
                            <a href="https://lungancatalin.github.io/cards/" target="_blank" rel="noopener noreferrer"><img src={Cards} alt="insure" /></a>
                        </div>
                    </div>
                </section>

                <section className="project-portfolio">
                    <div className="container-cards">
                        <div className="box-cards box-nine">
                            <a href="https://lungancatalin.github.io/base_demo/" target="_blank" rel="noopener noreferrer"><img src={Base} alt="base_apparel" /></a>
                        </div>
                        <div className="box-cards box-ten">
                            <a href="https://lungancatalin.github.io/covid19/" target="_blank" rel="noopener noreferrer"><img src={Covid19} alt="covid19" /></a>
                        </div>  
                    </div>
                </section>
        
            </main>
            
        </div>  
        )
    }    
}

