import React, {Component} from 'react';
import headerLogo from './images/logo.svg';
import Frontend from './images/frontend_img.svg';
import { Link } from 'react-scroll';


export default class HeaderSection extends Component {

    render() {
        return (
            <header className="top">
                <Link to="top" 
                    smooth={true}
                    duration={500}>
                    <button id="scroll-up" title="Go to top"><i className="fas fa-chevron-up" /></button>
                </Link>
                <div className="container-header">
                    <nav className="flex">
                        <div>
                            <img className="logo" src={ headerLogo } alt="logo" />
                        </div>

                        <div className="top-menu"> 
                            <ul className="flex flex-menu">
                                <Link 
                                    activeClass="active"
                                    to="about-me"
                                    spy={true}
                                    smooth={true}
                                    offset={0}
                                    duration={500}
                                    >
                                    <li>About</li>
                                </Link>

                                <Link 
                                    activeClass="active"
                                    to="project-portfolio"
                                    spy={true}
                                    smooth={true}
                                    offset={0}
                                    duration={500}
                                    >
                                    <li>Portfolio</li>
                                </Link>
                                <a className="resume" href={'https://drive.google.com/file/d/1Tb-FyWA0WuDDnwyTt-LXj1ank0reAg2i/view?fbclid=IwAR1GofCoNODniVJ9FOuZOlYRrU5kfSDPBYJUETm2KP8WIfGv7aWJLI3FNVM'}>Resume </a>
                            </ul>
                        </div>
                    </nav>

                    <div className="flex">
                        <div> 
                            <h1>Hi, <br/> 
                            I'm Lungan Catalin</h1>
                            <h2>Front End Developer</h2>
                            <h1 className="second">Nice to meet you!</h1>
                            <p>Passionate for travelling, I love helping people discover efficient solutions to their problems. I strongly believe that listening to our customers is the best way to improve our products.</p> 
                            
                            <ul>
                                <Link 
                                    activeClass="active"
                                    to="project-portfolio"
                                    spy={true}
                                    smooth={true}
                                    offset={0}
                                    duration={500}
                                    >
                                    <li className="btn btn-primary">See Projects</li>
                                </Link>
                            </ul>
                        </div>
                        <div>
                            <img className="header-img" src={ Frontend } alt="pc" />
                        </div>
                    </div>
                </div>
            </header>
        )
    }    
}

